import { Component, OnDestroy, OnInit } from '@angular/core';
import {AuthService} from './auth/auth.service';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnDestroy, OnInit{
  title = 'inventory-portal-poc';
  constructor(private authService: AuthService, private activeRoute: ActivatedRoute, private router: Router) { }
  
  accessToken : any;
  IsLoggedIn : boolean = false;
  subscription: Subscription;
  
  Logout() {
    this.authService.logout();
  }
  ngOnInit() {
    this.router.events.subscribe((e)=>{
      if(e instanceof NavigationEnd){
        console.log("NavigationEnd")
        console.log(e)

        this.accessToken = JSON.parse(window.localStorage.getItem("okta-token-storage"));
          if(this.accessToken && this.accessToken.access_token && this.accessToken.access_token.accessToken){
            this.IsLoggedIn =  true
          } else {
            this.IsLoggedIn =  false;
          }
      }
    });

    // this.subscription = this.authService.onLogout().subscribe(e => {
    //   console.log("trigger:",e);
    //   if(e == 'login'){
    //     this.IsLoggedIn =  true;
    //   } else {
    //     this.IsLoggedIn =  false;
    //   }
    // });
        // this.activeRoute.params.subscribe(routeParams => {
        //   this.accessToken = JSON.parse(window.localStorage.getItem("okta-token-storage"));
        //   if(this.accessToken && this.accessToken.access_token && this.accessToken.access_token.accessToken){
        //     this.IsLoggedIn =  true
        //   } else {
        //     this.IsLoggedIn =  false;
        //   }
        // });

    // console.log("accessToken: ", this.accessToken);
    // console.log("IsLoggedIn: ", this.IsLoggedIn);
  }
    ngOnDestroy() {
        // unsubscribe to ensure no memory leaks
        this.subscription.unsubscribe();
    }
}
