import { NgReduxRouter, NgReduxRouterModule } from '@angular-redux/router';
import {
  DevToolsExtension,
  NgRedux,
  NgReduxModule,
} from '@angular-redux/store';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { ErrorHandler, NgModule } from '@angular/core';
import {
  BrowserModule,
  HAMMER_GESTURE_CONFIG,
} from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { createEpicMiddleware } from 'redux-observable';

// import * as fromGuards from './guards';
import * as fromRedux from './redux';

/**
 * CoreModule
 */
@NgModule({
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    NgReduxModule,
    NgReduxRouterModule.forRoot(),
  ],
})
export class CoreModule {
  /**
   * constructor
   * @param {NgRedux<AppState>} ngRedux
   * @param {NgReduxRouter} ngReduxRouter
   * @param {RootEpics} rootEpics
   * @param {DevToolsExtension} devTools
   */
  constructor(
    private ngRedux: NgRedux<fromRedux.AppState>,
    private ngReduxRouter: NgReduxRouter,
    private devTools: DevToolsExtension,
  ) {
    const epicMiddleware = createEpicMiddleware();

    this.ngRedux.configureStore(
      fromRedux.rootReducer,
      fromRedux.INITIAL_STATE,
      [epicMiddleware],
      this.devTools.isEnabled()
        ? [
            this.devTools.enhancer({
              trace: true,
            }),
          ]
        : [],
    );

    if (this.ngReduxRouter) this.ngReduxRouter.initialize();
    // if (epicMiddleware) epicMiddleware.run(this.rootEpics.epics);
  }
}
