import { Injectable } from '@angular/core';
import { HttpClient, HttpParams, HttpHeaders, HttpClientModule, HttpErrorResponse} from '@angular/common/http';
import { Observable, throwError, of } from 'rxjs';
import {Md5} from "md5-typescript";
import 'url-search-params-polyfill';
import { catchError } from 'rxjs/operators';


@Injectable({
  providedIn: 'root'
})
export class ApiService {

  private apiURL: string;
  private securityKey: string;
  private xmlParam : any;
  private timeStamp : any;
  private jsonData: any;
  private cc: string;
  private arrFiles : Array<any>;
  private downloadURL: string;

  constructor( private httpClient : HttpClient ) {

    this.apiURL = 'https://eser0068/XDeltaAPI/API/obt_consulta?';
    this.securityKey = '4BABAE58-49EB-46F8-BDBB-ED6A6CB7E212'
    this.downloadURL = 'https://eser0068/XDeltaAPI/API/obt_docs?_id_aplicacion=';
  }

  loadXML(year : string  , month: string){

    var today = new Date().toJSON().slice(0,10).replace(/-/g,'')
 
    let date = new Date();
    let nextyear = parseInt(year) + 1;
    let startDate =  "01" + "/" + this.appendLeadingZeroes(eval(month)) + "/" + year;
    let endDate =  "01" + "/" + this.appendLeadingZeroes(eval(month)) + "/" + nextyear;
    // let endDate =  +  this.appendLeadingZeroes(this.lastday(eval(year), eval(month))) + "/" + this.appendLeadingZeroes(eval(month))+ "/" +year;
    
    console.log(startDate);
    console.log(endDate);

      let xmlPost = `<?xml version="1.0" encoding="UTF-8" ?><PETICION><APLICACIONES><GENERAL Salida="XML" maxDocs="10" /><APLICACION><PARAMETROS C128="" pB="1" Aplicacion="FACTURAS" /><CONSULTAS><CONSULTA><CAMPO valor="9713488" codigo="C79" /><CAMPO_DESDE valor="${startDate}" codigo="C81" /><CAMPO_HASTA valor="${endDate}"  codigo="C81" /></CONSULTA></CONSULTAS></APLICACION></APLICACIONES><PRESENTACION Orden="C79,C82,C81 desc" Bookmark="C79,C82,C81" Root="Documentos de Test" /></PETICION>`
    
      let _cc= Md5.init(xmlPost + '#' + today + '#' + this.securityKey);
      let controlCode =  this.formatStr(_cc,2).toUpperCase(); 

    let body = new URLSearchParams()
    body.set('_xml', xmlPost)
    body.set('_cc', controlCode);
    

  return this.httpClient.post(this.apiURL,
    body.toString(),
    {
      headers: new HttpHeaders()
        .set('Content-Type', 'application/x-www-form-urlencoded'),
        responseType: 'text'
    }
    ).pipe(catchError(this.errorHandler));
   }

  errorHandler(error: HttpErrorResponse) {
    if (error.status == 404 || error.status == 400) {
      return of(false);
    }
  }
  
  downloadDoc(appId: string, regId: string): Observable<any> {
    var today = new Date().toJSON().slice(0,10).replace(/-/g,'')
    let _cc= Md5.init(appId + '#' + regId +'#' + today + '#' + this.securityKey);
    let controlCode =  this.formatStr(_cc,2).toUpperCase(); 
    let url = this.downloadURL+appId+'&_id_registro='+regId+'&_cc=' + controlCode;
    return this.httpClient.get(url, { responseType: "blob" });
  }
  
 formatStr(str, n) {
    var a = [], start=0;
    while(start<str.length) {
     a.push(str.slice(start, start+n));
     start+=n;
    }
    return a.join("-");
 }

  appendLeadingZeroes(n){
  if(n <= 9){
    return "0" + n;
  }
  return n
  }

 lastday (y,m){
    return  new Date(y, m, 0).getDate();
 }


}
