import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  CanActivateChild,
  Router,
  RouterStateSnapshot,
  UrlTree,
} from '@angular/router';
import { AuthService } from '@app/auth/auth.service';
import { Observable } from 'rxjs';

/**
 * AuthProtectedGuard
 */
@Injectable({ providedIn: 'root' })
export class AuthProtectedGuard implements CanActivate, CanActivateChild {
  /**
   * constructor
   * @param {Router} router
   * @param {AuthService} authService
   */
  constructor(
    private router: Router,
    private authService: AuthService,
  ) {}

  /**
   * validateUserAuthentication
   * @param {RouterStateSnapshot} state
   * @returns {Promise<boolean | UrlTree>}
   */
  async validateUserAuthentication(
    state: RouterStateSnapshot,
  ): Promise<boolean | UrlTree> {
    console.log('* validateUserAuthentication *');
    const authed = await this.authService.getAccessToken();
    console.log('* validateUserAuthentication - authed => ', authed);
    if (!authed) {
      console.log('* validateUserAuthentication - DISPATCH LOGOUT *');
      return this.router.parseUrl('/login');
    }

    return true;
  }

  /**
   * canActivate
   * @param {ActivatedRouteSnapshot} route
   * @param {RouterStateSnapshot} state
   * @returns {Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean}
   */
  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot,
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean {
    return this.validateUserAuthentication(state);
  }

  /**
   * canActivateChild
   * @param {ActivatedRouteSnapshot} route
   * @param {RouterStateSnapshot} state
   * @returns {Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean}
   */
  canActivateChild(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot,
  ): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean {
    return this.validateUserAuthentication(state);
  }
}
