import { Component, OnInit } from '@angular/core';
import { AuthService } from '../auth.service';
import { Router } from '@angular/router';
import { environment } from '@env/environment';

/**
 * AuthSSOCallbackComponent
 */
@Component({
  selector: 'ecp-sso-callback',
  template: `
    <div class="loader">
      <div class="loader-icon">
        <div></div>
        <div></div>
        <div></div>
        <div></div>
      </div>
    </div>
  `,
})
export class AuthSSOCallbackComponent implements OnInit {
  /**
   * constructor
   * @param {AuthService} authService
   * @param vwService
   * @param {NgRedux<AppState>} redux
   * @param {Router} router
   */
  constructor(
    private authService: AuthService,
    private router: Router,
  ) {}

  /**
   * ngOnInit
   */
  ngOnInit(): void {
    this.authService
      .parseTokensFromURL()
      .then(tokens => {
        if (!tokens) { throw new Error('SSO Callback: No Tokens parsed'); }

        this.authService.setTokens(tokens);

        this.router.navigateByUrl(
            environment.okta.authRedirectUrl,
          { replaceUrl: true },
        );

        window.localStorage.removeItem('returnUrl');
      })
      .catch(err => {
        console.log('*******');
        console.log('AuthSSOCallbackComponent - LOGIN_ERROR');
        console.log('*******');

        this.router.navigateByUrl('/auth/logout', { replaceUrl: true });
      });
  }
}
