import { NgModule } from '@angular/core';
import {Routes, RouterModule, PreloadAllModules} from '@angular/router';
import { LoginComponent } from './login/login.component';
import { InvoiceComponent } from './invoice/invoice.component';
import { ForgotpasswordComponent } from './forgotpassword/forgotpassword.component';
import { EnterEmailForgotPasswordComponent } from './enterEmailForgotPassword/enterEmailForgotPassword.component';
import { NewpasswordComponent } from './newpassword/newpassword.component';
import {AuthProtectedGuard, AuthPublicGuard} from './auth/guards';

const routes: Routes = [
  {
    path: 'login',
    component: LoginComponent,
    canActivate: [AuthPublicGuard]
  },
  {
    path: 'invoice',
    component: InvoiceComponent,
    //  canActivate: [AuthProtectedGuard]
  },
  {
    path: 'forgotpasswordconfirm',
    component: ForgotpasswordComponent,
    canActivate: [AuthPublicGuard]
  },
  {
    path: 'forgotpassword',
    component: EnterEmailForgotPasswordComponent,
    canActivate: [AuthPublicGuard]
  },
  {
    path: 'newpassword',
    component: NewpasswordComponent
  },
  {
    path: '',
    redirectTo: '/invoice',
    pathMatch: 'full'
  },
];

@NgModule({
  imports: [RouterModule.forRoot(
    routes,
    {
      enableTracing: true,
      useHash: false,
      preloadingStrategy: PreloadAllModules,
      scrollPositionRestoration: 'enabled',
      anchorScrolling: 'enabled',
      scrollOffset: [0, 70], // [x, y]
    } // <-- debugging purposes only
  )],
  exports: [RouterModule]
})
export class AppRoutingModule { }
