import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import xml2js from 'xml2js';
import { isObject } from 'util';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-download',
  templateUrl: './invoice.component.html',
  styleUrls: ['./invoice.component.scss']
})
export class InvoiceComponent implements OnInit {

  private objData : Array<String>;
  private arrFiles : Array<any>;
  private arrFiltered : Array<any>;
  private year : string;
  private month : string;
  private msg : String;

  constructor(private apiService : ApiService, private datePipe: DatePipe) {
    this.arrFiles = [];
    this.arrFiltered = [];
   }

  ngOnInit() {

  }


  renderList() {
    
    this.apiService.loadXML(this.year, this.month).subscribe((data) => {
      //console.log(data)
    
     
      this.arrFiles = [];      
      let result;
      let parser = xml2js;
     

        parser.Parser().parseString(data, (e, r) => {result = r});

        this.objData = result;

        for(let documento in this.objData) {

          for(let consulta in this.objData[documento]) {

            if(consulta === 'CONSULTA') {

              for(let lista in this.objData[documento][consulta][0]['LISTA'][0]['BOOKMARK'][0]['BOOKMARK']) {

                for(let bookmark in this.objData[documento][consulta][0]['LISTA'][0]['BOOKMARK'][0]['BOOKMARK'][lista]) {

                  for(let innerDocumento in this.objData[documento][consulta][0]['LISTA'][0]['BOOKMARK'][0]['BOOKMARK'][lista][bookmark] ) {

                    let innerObj = this.objData[documento][consulta][0]['LISTA'][0]['BOOKMARK'][0]['BOOKMARK'][lista][bookmark][innerDocumento];

                    if (isObject(innerObj))  {
                      for (let val in innerObj) {

                          for(let content in innerObj[val][0]) {
                            let file = innerObj[val][0][content];
                            innerObj[val][0][content].C81 = innerObj[val][0][content].C81.replace(" 00:00:00", "");  
                            this.arrFiles.push(innerObj[val][0][content])
                          }
                      }
                    }
                  }
                }
              }
            }
          }
        } 
        if (this.arrFiles.length > 0)
        {
          this.msg ="";
        }
        else{
          this.msg ="No invoice found";
        }
      });
      console.log("this.arrFiles")
      console.log(this.arrFiles)
  }

  setYear(year) {
    this.year = year;
  }

  setMonth(month) {
      this.month = month;
    // this.endDate = endDate;
  }
  getInvoiceList(year, month) {
    this.renderList()
  }

  download(appId:string, regId: string) {
    this.apiService.downloadDoc(appId, regId).subscribe(result => {
        var url = window.URL.createObjectURL(result);
        window.open(url);
        console.log("download result ", result);
    });
  }
}
