import { Component, OnInit } from '@angular/core';
import {AbstractControl, FormBuilder, FormGroup} from '@angular/forms';
import {AuthService} from '../auth/auth.service';
import {Router} from '@angular/router';
import {NgRedux, select} from "@angular-redux/store";
import {AppState} from "../core/redux";
import {AuthActionTypes} from "../core/redux/actions";
import {Observable} from "rxjs";
import { environment } from '@env/environment';
import OktaAuth from '@okta/okta-auth-js';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})
export class LoginComponent implements OnInit {
  loginForm: FormGroup;
  @select(['auth', 'error'])
  error$: Observable<any>;

  public authClient: any;

  constructor(private fb: FormBuilder, private authService: AuthService, private router: Router, private redux: NgRedux<AppState>) { 
    this.authClient = new OktaAuth(environment.okta.client);
  }
  get username(): AbstractControl {
    return this.loginForm.get('username');
  }
  get password(): AbstractControl {
    return this.loginForm.get('password');
  }
  ngOnInit() {
    this.loginForm = this.createForm();
  }
  private createForm() {
   return this.fb.group({
     username: [''],
     password: ['']
    });
  }
  
  
  
  gotoEnterPasswordInFOrgotPassword() : void {
    this.router.navigateByUrl('/forgotpassword');
  }

  startLogin() {
    if (this.loginForm.valid) {
      // check for remember me and set the localstorage
      // if (this.rememberMeFlag) {
      //   window.localStorage.setItem(
      //     'username',
      //     this.loginForm.get('username').value.toString(),
      //   );
      // }
      // this.redux.dispatch({ type: AuthActionTypes.LOGIN });

      this.authService.login(this.loginForm.value).catch(err => {
        console.log('*******');
        console.log('LoginFormComponent - LOGIN_ERROR');
        console.log('*******');
        this.redux.dispatch({
          type: AuthActionTypes.LOGIN_ERROR,
          payload: err,
        });
        this.router.navigateByUrl('/');
        // this.router.navigate(['/'])
      });
    }

    return false;
  }
}
