import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {AuthService} from '../auth/auth.service';
import {Observable} from "rxjs";
import {Validators , AbstractControl, FormBuilder, FormGroup, FormControl} from '@angular/forms';

@Component({
  selector: 'app-enterEmailForgotPassword',
  templateUrl: './enterEmailForgotPassword.component.html',
  styleUrls: ['./enterEmailForgotPassword.component.scss']
})
export class EnterEmailForgotPasswordComponent implements OnInit {
  forgotPasswordForm: FormGroup;
  // @select(['auth', 'error'])
  error$: Observable<any>;
  constructor(private fb: FormBuilder, private router: Router, private authService : AuthService) { }

  get username(): AbstractControl {
    return this.forgotPasswordForm.get('username');
  }
  ngOnInit() {
  //   this.forgotPasswordForm = new FormGroup({
  //     username: new FormControl()
  //  });
    this.forgotPasswordForm = this.createForm();
  }

  private createForm() {
    return this.fb.group({
      username: ['', [
        Validators.required,
        Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,50}$")]]
     });
   }


   forgotpassword() : void {
     if(this.forgotPasswordForm.valid){
       console.log("username", this.forgotPasswordForm.value.username);
       this.authService.forgotPassword(this.forgotPasswordForm.value.username);
     }

  }
}